import React, { Component } from 'react'
import { Row, Col } from 'antd'
import ColorBox from './color-box'
import DemoShower from '../../demo-shower'

const colors1 = [{
  name: '@primary-color',
  rgb: '#1973BA',
  desc: '/',
  whiteFont: true
}, {
  name: '@color-1',
  rgb: '#EDF4FA',
  desc: ''
}, {
  name: '@color-5',
  rgb: '#5094CB',
  desc: ''
}, {
  name: '@color-6',
  rgb: '#1973BA',
  desc: '',
  whiteFont: true
}, {
  name: '@color-7',
  rgb: '#1661A1',
  desc: '',
  whiteFont: true
}]

const colors2 = [{
  name: '@page-background',
  rgb: '#ECECEC',
  desc: ''
}, {
  name: '@white',
  rgb: '#FFFFFF',
  desc: '/'
}, {
  name: '@black',
  rgb: '#000000',
  desc: '',
  whiteFont: true
}]

const colors3 = [{
  name: '@primary-color',
  rgb: '#1973BA',
  desc: '/'
}, {
  name: '@color-success',
  rgb: '#00A854',
  desc: '',
  whiteFont: true
}, {
  name: '@color-warn',
  rgb: '#FFBF00',
  desc: ''
}, {
  name: '@color-error',
  rgb: '#F04134',
  desc: '',
  whiteFont: true
}]

const lessStr = `
.color-box {
  height: 96px;
  line-height: @gap-3;
  padding-top: @gap-2;
  text-align: center;
  border-radius: 4px;
  border: 1px solid #000;
  color: @black;

  &--success {
    color: @white;
    background: @color-success;
  }

  &--warn {
    color: @white;
    background: @color-warn;
  }
}`.trim()

class ColorsDemo extends Component {
  render () {
    return (
      <div>
        <h1 className='margin-t-2 margin-b-2'></h1>
        <hr />
        <h2 className='margin-t-2 margin-b-2'></h2>
        <Row gutter={16}>
          {
            colors1.map((item, idx) => {
              return (
                <Col key={idx} className='gutter-row' span={4}>
                  <ColorBox color={item} />
                </Col>
              )
            })
          }
        </Row>

        <h2 className='margin-t-2 margin-b-2'></h2>
        <Row gutter={16}>
          {
            colors2.map((item, idx) => {
              return (
                <Col key={idx} className='gutter-row' span={4}>
                  <ColorBox color={item} />
                </Col>
              )
            })
          }
        </Row>

        <h2 className='margin-t-2 margin-b-2'></h2>
        <Row gutter={16}>
          {
            colors3.map((item, idx) => {
              return (
                <Col key={idx} className='gutter-row' span={4}>
                  <ColorBox color={item} />
                </Col>
              )
            })
          }
        </Row>

        <DemoShower title='' desc='' less={lessStr}>
          <Row gutter={16}>
            <Col className='gutter-row' span={6}>
              <div className='color-box color-box--success'>
                <div>@color-success</div>
                <div></div>
              </div>
            </Col>

            <Col className='gutter-row' span={6}>
              <div className='color-box color-box--warn'>
                <div>@color-warn</div>
                <div></div>
              </div>
            </Col>
          </Row>
        </DemoShower>
      </div>
    )
  }
}

export default ColorsDemo



