import React, { Component } from 'react'
import {Row, Col, Card } from 'antd'
import DemoShower from '../demo-shower'

class CardDemo extends Component {
  render () {
    return (
      <div>
        <h1>Card</h1>
        <hr />
        <DemoShower title='Card' desc=',Hover'>
          <Row gutter={16}>
            <Col className='gutter-row' span={8}>
              <Card title='' noHovering >
                <p className='p1'>card</p>
                <p>card</p>
              </Card>
            </Col>

            <Col className='gutter-row' span={8}>
              <Card title='' noHovering className='tws-card-primary'>
                <p className='p1'>card</p>
                <p>card</p>
              </Card>
            </Col>

            <Col className='gutter-row' span={8}>
              <Card title='' noHovering className='tws-card-default'>
                <p className='p1'>card</p>
                <p>card</p>
              </Card>
            </Col>

          </Row>
        </DemoShower>
      </div>
    )
  }
}

export default CardDemo



