import React, {Component} from 'react';
import {Table, Timeline, Button} from 'antd';

class SubmitHistory extends  Component{
  render() {
    return (
        <Timeline>
          <Timeline.Item color="green">
            2015-09-01 10:00  /4 
            <p>: http://www.github.com/aaabbb</p>
          </Timeline.Item>
          <Timeline.Item color="green">
            2015-09-01 10:00  /3 
          </Timeline.Item>
          <Timeline.Item color="red">
            <p>2015-09-01 10:00  /3 </p>
            <p>: http://www.github.com/aaabbb</p>
            <p><Button type="primary"></Button></p>
          </Timeline.Item>
          <Timeline.Item color="green">
            <p>2015-09-01 10:00  /3 </p>
            <p>: http://www.github.com/aaabbb</p>
          </Timeline.Item>
        </Timeline>
    )
  }
};

class ScoreSheetTable extends Component {
  render() {
    const dataSource = [{
      key: '1',
      name: '',
      age: 5,
      address: 10
    }, {
      key: '2',
      name: '',
      age: 5,
      address: '20'
    }];

    const columns = [{
      title: '',
      dataIndex: 'name',
      key: 'name',
      description: "a"
    }, {
      title: '',
      dataIndex: 'age',
      key: 'age',
      description: "a"
    }, {
      title: '',
      dataIndex: 'address',
      key: 'address',
      description: "a"
    }];


    return (
        <Table dataSource={dataSource} columns={columns} expandedRowRender={record => <SubmitHistory />}/>
    )
  }
}

export default ScoreSheetTable;



